.section-wrapper {
    margin: 1rem ;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container img {
    width: 95vw;
    height: auto;
    box-shadow: 0 5px 5px -5px black;
}

.container p {
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 1.2rem;
    align-items: center;
    position: absolute;
    height: 5rem;
    width: 95vw;
    background-color: rgba(255, 213, 25, 0.808);
}