nav {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255,213,25);
    padding: 0.8rem 0.5rem;
    height: 3.5rem;
    position: sticky;
    top: 0;
    border-bottom: 1px solid black;
    margin-bottom: 0.5rem;
    z-index: 10;
}

.left-side {
    width: 100%;
}

.logo-nav {
    padding-left: 3rem;
}

nav #close .hidden-links{
    display: none;
}

nav #close .logo-nav{
    display: flex;
    height: 50px; 
}

nav #open .hidden-links{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

nav #open .logo-nav {
   display: none;
}

.right-side{
    display: none;
    align-items: center;
    width: 60%;
    justify-content: space-around;
}

.right-side img{
    display: none;
}

.nav-icon {
    height: auto;
    width: 30px; 
    padding: 0 0.1rem;
}

.shoe {
    height: 40px;
    width: auto;
}

.giftbox{
    height: 27px;
    width: auto;
}

button {
    display: flex;
    border: none;
    background-color: rgb(255,213,25);
    padding-right: 1.2rem;
    cursor: pointer;
}

@media only screen and (min-width: 600px) {

    nav #open .hidden-links{
        display: none;
    }

    nav #open .logo-nav {
        display: flex;
        height: 50px;
    }

    .right-side{
        display: flex;
        padding-right: 3rem;
        max-width: 350px;
    }

    .right-side img {
        display: flex;
    }

    .nav-icon {
        height: auto;
        width: 35px; 
        padding: 0 0.1rem;
    }
    
    .shoe {
        height: 45px;
        width: auto;
    }
    
    .giftbox{
        height: 32px;
        width: auto;
    }

    button {
        display: none;

    }
}


